.div1 {
  display: grid;
  align-items: center;
  justify-content: center;
  grid-template-columns: 800px 450px;
  padding: 5px;
  grid-gap: 7px;
}
.div1image1 {
  background-size: cover;
  background-repeat: no-repeat;
  height: 520px;
  color: white;
  display: flex;
  flex-flow: column;
  justify-content: center;
  justify-content: center;
  border-radius: 7px;
}
.twoimages {
  display: grid;
  grid-template-columns: 450px;
  grid-template-rows: 260px 260px;
  grid-gap: 5px;
}
.div1image2 {
  border-radius: 8px;
  background-size: cover;
  background-repeat: no-repeat;
}
