.left {
  display: grid;
  border-radius: 7px;
  grid-template-columns: 400px 350px;
  grid-gap: 10px;
  margin: 10px;
  margin-left: 50px;
  padding: 5px;
  text-align: left;
  border-bottom: 4px solid rgb(80, 3, 12);
}
.leftimage {
  background-size: cover;
  background-repeat: no-repeat;
  height: 260px;
}
.seconddiv {
  display: grid;
  grid-template-columns: 400px;
  grid-template-rows: 60px 160px 20px;
}
.right {
  display: grid;
  grid-template-columns: 250px 150px;
  width: 455px;
  margin: 10px;
  margin-left: 0px;
  border-radius: 7px;
  padding: 5px;
  border-bottom: 2px solid rgb(80, 3, 12);
  text-align: left;
  grid-gap: 10px;
}
.secondrightdiv {
  display: grid;
  grid-template-columns: 180px;
  grid-template-rows: 80px 50px;
}
.rightimage {
  background-size: cover;
  background-repeat: no-repeat;
  height: 150px;
}
.ad {
  text-align: center;
  height: 500px;
  width: 350px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
  position: relative;
  margin-left: 60px;
}

.maindiv {
  display: grid;
  grid-template-columns: auto auto;
  grid-gap: 15px;
  margin: 10px;
  text-align: center;
}
.protoh2 {
  text-align: left;
  margin-left: 50px;
  padding: 5px;
  border-bottom: 2px solid rgb(80, 3, 12);
  width: 170px;
  border-radius: 7px;
}
