.div3proto {
  display: grid;
  grid-template-columns: 280px 350px;
  grid-template-rows: 180px;
  grid-gap: 24px;
  border-bottom: 3px solid rgb(233, 221, 212);
}
.div3image {
  background-repeat: no-repeat;
  background-size: cover;
}
.div3div2 {
  display: grid;
  grid-template-columns: 350px;
  grid-template-rows: 33px 120px 10px;
  justify-content: center;
  align-items: center;
  text-align: left;
}
.ads {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 650px;
  border: 2px solid black;
  height: 200px;
}
.div3real {
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: auto auto;
  margin: 27px;
  grid-gap: 10px;
}
