.upper {
  position: relative;
  left: 490px;
  display: flex;
  flex-flow: column;
  margin: 10px;
  padding: 10px;
  width: 450px;
}
.upperimage {
  background-size: cover;
  height: 230px;
  background-repeat: no-repeat;
}

.bottompro {
  display: grid;
  grid-template-columns: 400px;
  grid-template-rows: 80px 230px 200px 30px;
  margin: 10px;
  padding: 6px;
}
.bottomimage {
  background-size: cover;
  background-repeat: no-repeat;
}
.bottom {
  display: flex;
  flex-flow: row wrap;
  margin: 10px;
  margin-left: 70px;
}
