.nav {
  align-items: center;
  justify-content: center;
  margin: 0px;
  background-color: rgb(233, 221, 212);
  text-align: center;
  padding: 10px;
}
.navul {
  display: flex;
  list-style: none;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-evenly;
}

li:after {
  display: block;
  content: "";
  border-bottom: solid 3px #019fb6;
  transform: scaleX(0);
  transition: transform 250ms ease-in-out;
}
li:hover:after {
  transform: scaleX(1);
}
li.fromRight:after {
  transform-origin: 100% 50%;
}
li.fromLeft:after {
  transform-origin: 0% 50%;
}
